.bass-key-wrap{position:relative;display:flex;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;margin-right:0.3%}.bass-key-wrap:hover input ~ .bass-key{background-color:#ccc}.bass-key-wrap input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.bass-key-wrap input:checked ~ .bass-key{background-color:#ffd95b}.bass-key{display:flex;top:0;left:0;height:26px;width:10px;background-color:#eee;border-radius:3px;border:2px solid black}.bass-row{display:flex;justify-content:center;align-items:center;margin-bottom:20px;width:560px}.bass-synth-wrap{border:solid 5px #f9d254;width:560px;margin-top:1px;margin-left:0px;padding-top:1%;background-color:black;margin-right:auto;padding-bottom:0;box-shadow:-1px 1px #ffb22e, -2px 2px #ffb22e, -3px 3px #ffb22e, -4px 4px #ffb22e, -5px 5px #ffb22e}.bass-bar-headers{display:flex;height:28px}.bass-bar-headers-2{display:flex;height:29px;margin-top:-33px}.bass-bar-headers p{width:26.5px;margin-left:30px;margin-right:0px;padding:3px;color:white;font-weight:bold}.bass-bar-headers-2 p{width:26.5px;margin-left:30px;margin-right:0px;padding:3px;color:white;font-weight:bold}.arrows{margin-bottom:9px;display:flex}.bass-logo{font-family:"Courier New", Courier, monospace;border-top:5px solid  #f9d254;border-bottom:4px solid  #f9d254;background-color:#af2b5e;display:flex;justify-content:space-around}.bass-logo h2{margin-left:0px}.bass-instrument-name p{margin-bottom:-23px;margin-left:5px;color:white}.bass-btn{width:60px;height:34px;font-family:"Courier New", Courier, monospace;margin-top:12px;border:2px solid black;font-weight:bold;background-color:mediumpurple;cursor:pointer}.bass-btn:hover{background-color:yellow}.bass-btn:focus{outline:none;box-shadow:none}.bass-effects-wrap{display:flex;justify-content:space-between;width:400px;margin:auto}.bass-slider label{font-family:"Courier New", Courier, monospace;color:white;font-weight:bold;font-size:12px;margin:auto}
