.drum-key-wrap{position:relative;display:flex;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;margin-right:0.3%}.drum-key-wrap:hover input ~ .drum-key{background-color:#ccc}.drum-key-wrap input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.drum-key-wrap input:checked ~ .drum-key{background-color:#e16e6a}.drum-key{display:flex;top:0;left:0;height:26px;width:10px;background-color:#eee;border-radius:3px;border:2px solid black}.row{display:flex;justify-content:center;align-items:center;margin-bottom:20px}.drum-machine-wrap{border:solid 5px #e16e6a;width:560px;margin-top:38px;margin-left:auto;padding-top:1%;background-color:#676767;margin-right:auto;padding-bottom:0;box-shadow:-1px 1px #805100, -2px 2px #805100, -3px 3px #805100, -4px 4px #805100, -5px 5px #805100}.bar-headers{display:flex;height:27px}.bar-headers p{width:26.5px;margin-left:30px;margin-right:0px;padding:3px;color:white;font-weight:bold}.arrows{margin-bottom:9px;display:flex}.drum-logo{font-family:"Courier New", Courier, monospace;border-top:5px solid  #e16e6a;border-bottom:4px solid  #e16e6a;background-color:#896262;display:flex;justify-content:space-evenly}.drum-logo h2{margin-left:0px}.drums-instrument-name p{margin-bottom:-23px;margin-left:5px;color:white}.drums-btn{width:60px;height:34px;font-family:"Courier New", Courier, monospace;margin-top:12px;border:2px solid black;font-weight:bold;background-color:mediumpurple;cursor:pointer}.drums-btn:hover{background-color:yellow}.drums-btn:focus{outline:none;box-shadow:none}.drum-sample-btn{font-family:"Courier New", Courier, monospace;font-weight:bold;background-color:mediumpurple;cursor:pointer}.drum-sample-btn:hover{background-color:yellow}.drum-sample-btn:focus{outline:none;box-shadow:none}.drum-samples label{font-family:"Courier New", Courier, monospace;color:white;font-weight:bold;font-size:12px;margin:auto}
